
.recommend_box{
  width: 100%;
  height: auto;
  border: 1px solid #e4e0eb;
  margin-bottom: 20px;
}
.recommend_box_head{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  padding: 0 20px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #fcfcfc;
  border-bottom: 1px solid #e4e0eb;
}
.recommend_box_head span{
  color: #616161;
  font-size: 15px;
}
.recommend_box_head a{
  color: #b3b3b3;
  font-size: 13px;
}
.hot_news_item,.rec_organization_item,.rec_firm_item{
  margin: 0 20px;
  padding: 16px 0;
  border-bottom: 1px dashed #e4e0eb;
}
.hot_news_item a,.rec_organization_item a,.rec_firm_item a{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 22px;
  color: #898989;
}
.hot_news_item:last-child,.rec_organization_item:last-child,.rec_firm_item:last-child{
  border-bottom: 0;
}
.dot{
  font-size: 50px;
  padding-right: 10px;
}
.hot_news_item p,.rec_firm_item p{
  font-size: 14px;
}
.hot_news_item p span{
  color: #d6d6d6;
}
.rec_organization_item a img{
  width: 50px;
  height: 50px;
  padding-right: 10px;
}
.rec_firm_item{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}



