
.manage_nav_wrapper{
  width: 100%;
  height: 67px;
  background: #5385d5;
}
.manage_nav{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 1210px;
  height: 67px;
  margin: auto;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.manage_logo{
  width: 128px;
  height: 45px;
}
.manage_nav section:nth-child(2) a{
  display: inline-block;
  height: 67px;
  line-height: 67px;
  padding: 0 20px;
  color: #fff;
  font-size: 16px;
}
.manage_select{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 385px;
  height: 32px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.manage_select select{
  width: 110px;
  height: 32px;
  border: none;
  font-size: 13px;
  color: #b3b3b3;
}
.manage_select input{
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  height: 32px;
  border: none;
  padding: 0;
  padding-left: 15px;
  border-left: 1px solid #f1f1f1;
  font-size: 13px;
}
.manage_select_but{
  width: 80px;
  height: 32px;
  text-align: center;
  line-height: 32px;
  background: #6398ed;
  color: #fff;
  font-size: 14px;
}
.manage_login{
  width: 145px;
  text-align: center;
}
.manage_login .unlogin{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 145px;
  height: 67px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.manage_login .unlogin a{
  font-size: 14px;
  color: #fff;
  padding: 0 10px;
}
.loged{
  width: 145px;
  height: 67px;
  position: relative;
  cursor: pointer;
}
.loged_infor{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 145px;
  height: 67px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.loged_infor .user_icon{
  width: 29px;
  height: 29px;
  border-radius: 100%;
}
.loged_infor .username{
  font-size: 14px;
  color: #fff;
}
.loged_infor .icon-angle-down{
  color: #fff;
  font-size: 12px;
}
.loged_menu{
  position: absolute;
  width: 143px;
  height: auto;
  border: 1px solid #e5e7ed;
  border-top: 0;
  top: 67px;
  left: 0;
}
.loged_menu a{
  display: block;
  width: 100%;
  height: 48px;
  color: #898989;
  font-size: 16px;
  text-align: center;
  line-height: 48px;
  border-top: 1px solid #e5e7ed;
  background: #fff;
}
.loged_menu a.active,.loged_menu a:hover{
  color: #6398ed;
}
.loged_menu a span{
  display: inline-block;
  width: 30px;
  font-size: 18px;
}



