
.help_content_header{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.help_content_header p:nth-child(1){
  width: 125px;
  height: 40px;
  border-top: 2px solid #6398ed;
  color: #6398ed;
  font-size: 15px;
  text-align: center;
  line-height: 40px;
}
.help_content_header p:nth-child(2){
  padding-right: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border-left: 1px solid #e5e7ed;
  border-bottom: 1px solid #e5e7ed;
  color: #6398ed;
  font-size: 13px;
  background: #fcfcfc;
}
.help_content_header p:nth-child(2) span:nth-child(1){
  font-size: 16px;
  padding-right: 5px;
}



