
.news_list{
  padding-bottom: 30px;
}
.news_item{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;  /*改动, 靠左对齐*/
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 20px 0;
  margin: 0 20px;
  border-bottom: 1px dashed #e5e7ed;
}
.news_item img{
  min-width: 160px;
  height: 106px;
}
.news_item .news_infor{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 106px;
  padding-left: 20px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.news_item .news_infor a{
  cursor: pointer;
}
.news_item .news_infor .news_tit{
  font-size: 18px;
  color: #616161;
}
.news_item .news_infor .news_des{
  font-size: 14px;
  color: #b3b3b3;
  line-height: 24px;
}
.news_item .news_infor .news_time{
  font-size: 11px;
  color: #d6d6d6;
}
.list-pagination-wrap{
  padding-top: 15px;
  padding-bottom: 15px;
  text-align: center;
}
.list-pagination{
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 4px;
  background-color: #fbfbfb;
  font-size:0em;
}
.list-pagination li{
  

  display: inline-block;
  min-width: 32px;
  height: 30px;
  text-align: center;
  line-height: 28px;
  padding: 2px;
  /*border-left: 1px solid #d00;*/
  box-sizing: border-box;
  border-right: 1px solid #ccc;

  font-size:11px;
  color: #aaa;
}
.list-pagination li:last-child{
  border-right: none;
}
.list-pagination li.active{
  background-color: #559CF1;
  color: white;
}



