
.bannerBox{
  position: absolute;
  width: 100%;
  left: 0;
  min-width: 1210px;
}
.swiper-container {
  width: 100%;
  height: 408px;
}
.swiper-wrapper {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.swiper-slide img {
  width: 100%;
  height: 100%;
}




