
.vfooter {
  width: 100%;
  min-width: 1210px;
  border-top: 1px solid #e5e7ed;
  background: #f5f5f5;
}
.footer_main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 1210px;
  margin: auto;
  padding: 50px 0 40px 0;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}
.footer_main section:nth-child(1) {
  width: 396px;
}
.footer_main section:nth-child(2),
.footer_main section:nth-child(3),
.footer_main section:nth-child(4) {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.footer_main section:last-child {
  width: 118px;
}
.footer_main section:last-child img {
  width: 118px;
  height: 118px;
}
.footer_main section:last-child span {
  display: block;
  line-height: 30px;
  color: #616161;
  font-size: 14px;
  text-align: center;
}
.footer_label {
  display: block;
  color: #616161;
  font-size: 18px;
  padding-bottom: 12px;
}
.footer_label_1 {
  font-weight: 600;
}
.footer_main section a {
  display: block;
  line-height: 30px;
  font-size: 14px;
  color: #898989;
}
.friend_link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 1210px;
  height: 45px;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: auto;
  border-top: 1px solid #e5e7ed;
}
.friend_link span {
  font-size: 13px;
  color: #6b6b6b;
}
.friend_link a {
  font-size: 13px;
  color: #989898;
  padding: 0 10px;
}
.archival {
  width: 100%;
  height: 40px;
  line-height: 40px;
  color: #9398a1;
  background: #4c4f54;
  text-align: center;
}



