
.list-pagination-wrap{
    padding-top: 15px;
    padding-bottom: 15px;
    text-align: center;
}
.list-pagination{
    display: inline-block;
    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fbfbfb;
    font-size:0em;
}
.list-pagination li{
    

    display: inline-block;
    min-width: 32px;
    height: 30px;
    text-align: center;
    line-height: 28px;
    padding: 2px;
    /*border-left: 1px solid #d00;*/
    box-sizing: border-box;
    border-right: 1px solid #ccc;

    font-size:11px;
    color: #aaa;
}
.list-pagination li:last-child{
    border-right: none;
}
.list-pagination li.active{
    background-color: #559CF1;
    color: white;
}




