
.main_nav_wrapper{
  width: 100%;
  height: 40px;
  border-bottom: 2px solid #5385d5;
  background: #ffffff;
}
.main_nav{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 1210px;
  height: 42px;
  margin: auto;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #5385d5;
  color: #fff;
  font-size: 15px;
}
.main_nav p{
  height: 42px;
}
.main_nav div:nth-child(1){
  width: 226px;
  height: 42px;
  line-height: 42px;
  cursor: pointer;
  background: #6398ed;
  position: relative;
}
.main_nav div:nth-child(1) span:nth-child(1){
  float: left;
  padding-left: 20px;
}
.main_nav div:nth-child(1) span.icon-angle-down{
  height: 42px;
  line-height: 42px;
  font-size: 12px;
  float: right;
  padding-right: 20px;
}
.main_nav p:nth-child(2){
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.main_nav p:nth-child(2) a{
  width: 112px;
  height: 42px;
  line-height: 42px;
  text-align: center;
  cursor: pointer;
  color: #fff;
}
.main_nav p:nth-child(2) i{
  height: 16px;
  width: 1px;
  background: #7099da;
}
.main_nav p:nth-child(3){
  width: 150px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer;
}
.main_nav p:nth-child(3) span:nth-child(1){
  padding-right: 10px;
}
.items_num{
  width: 15px;
  height: 15px;
  border-radius: 100%;
  background: #ff8a6e;
  margin-left: 5px;
  text-align: center;
  line-height: 15px;
  font-size: 14px;
}
.main_drop_down{
  position: absolute;
  width: 226px;
  height: auto;
  left: 0;
  top: 42px;
  z-index: 9;
}
.main_drop_down section{
  padding: 10px 20px 20px 20px;
  background: #5385d5;
  border-bottom: 1px solid #6696e4;
}
.main_drop_down section:last-child{
  border: 0;
}
.main_drop_down section p{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 30px;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 14px;
}
.main_drop_down section p span:nth-child(2){
  padding-left: 10px;
}
.main_drop_down section a{
  float: left;
  line-height: 20px;
  font-size: 12px;
  padding-right: 14px;
  color: #fff;
}
.slide-fade-enter-active {
  transition: all .3s ease;
}
.slide-fade-leave-active {
  transition: all .3s;
  opacity: 0;
}
.slide-fade-enter{
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
  opacity: 0;
}



