
.collect_list{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.collect_list section{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 450px;
  height: 90px;
  margin-bottom: 20px;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border: 1px solid #e5e7ed;
  background: #f9fafc;
}
.collect_list section img{
  width: 60px;
  height: 60px;
  padding: 20px;
}
.collect_list section div{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-flow: 1;
      flex-flow: 1;
}
.collect_list section div a{
  font-size: 16px;
  line-height: 32px;
  color: #616161;
}
.collect_list section div span{
  font-size: 14px;
  line-height: 22px;
  color: #898989;
  cursor: pointer;
}



