
.my_company_list li{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 20px;
  margin-bottom: 20px; 
  border: 1px solid #e5e7ed;
  background: #f9fafc;
}
.my_company_list li img{
  width: 110px;
  height: 110px;
  border: 1px solid #e5e7ed;
  margin-right: 20px;
}
.my_company_list li section{
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.my_company_list li section h1{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 32px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-bottom: 10px;
}
.my_company_list li section h1 a{
  font-size: 16px;
  color: #616161;
}
.my_company_list li section h1 span{
  font-size: 14px;
}
.my_company_list li section h1 span span.auditing{
  color: #ff8a6e;
}
.my_company_list li section h1 span span.audited a{
  color: #6398ed;
  font-size: 14px;
  padding-right: 10px;
}
.my_company_list li section h1 span span.audited span{
  color: #898989;
  font-size: 14px;
  cursor: pointer;
}
.my_company_list li section h1 span a.claim{
  padding: 5px 20px;
  color: #87ce76;
  font-size: 14px;
  border: 1px solid #87ce76;
  border-radius: 4px;
}
.my_company_list li section p{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 30px;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 15px;
  color: #b3b3b3;
}
.my_company_list li section p span:nth-child(2n){
  padding: 0 25px 0 10px;
}



