
.company .tab_type_box{
  border-bottom: 0;
}
.company_selet_box{
  width: 962px;
  border: 1px solid #e5e7ed;
  border-top: 0;
  padding: 20px 0;
}
.company_selet_box section{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 36px;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-left: 20px;
  color: #898989;
  font-size: 14px;

  -ms-flex-wrap: wrap;

      flex-wrap: wrap;
}
.company_selet_box section p{
  width: 90px;
}
.company_selet_box section span{
  height: 22px;
  line-height: 22px;
  padding: 0 9px;
  cursor: pointer;
}
.company_selet_box section span.active{
  border-radius: 4px;
  background: #6398ed;
  color: #fff;
}
.company_list_box{
  margin-top: 30px;
}
.company_list_head span{
  color: #ff8a6e;
}



