
.indexLoginWrapper {
  position: absolute;
  width: 1210px;
  height: 408px;
  top: 0;
  left: 50%;
  margin-left: -605px;
}
.indexLogin {
  position: absolute;
  right: 0;
  top: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 226px;
  height: 381px;
  background: #ffffff;
  z-index: 10;
}
.indexLogin .userIcon {
  width: 70px;
  height: 70px;
  border-radius: 100%;
}
.indexLogin .userName {
  font-size: 14px;
  color: #b3b3b3;
  padding: 10px 0;
}
.indexLogin .btns {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 36px;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.indexLogin .btns .userBtn {
  display: block;
  width: 90px;
  height: 34px;
  border-radius: 34px;
  cursor: pointer;
  text-align: center;
  line-height: 34px;
  font-size: 13px;
  margin: 0 5px;
}
.indexLogin .btns .loginBtn {
  border: 1px solid #337ffb;
  color: #337ffb;
}
.indexLogin .btns .registerBtn {
  border: 1px solid #ffbe61;
  color: #ffbe61;
}
.indexLogin .introduceLine {
  display: block;
  margin-left: 50px;
  border-top: 1px solid #e6e6e6;
}
.indexLogin .introduceDiv {
  width: 100%;
  border-top: 1px solid #e6e6e6;
  margin-top: 15px;
}
.indexLogin .introduceItem {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 95px;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.indexLogin .introduceItem .introduceIcon {
  width: 40px;
  margin-right: 15px;
}
.indexLogin .introduceItem p {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 120px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.indexLogin .introduceItem p span:nth-child(1) {
  font-size: 14px;
  color: #898989;
}
.indexLogin .introduceItem p span:nth-child(2) {
  font-size: 18px;
  color: #616161;
}



