
.consignee_address{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  height: 110px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 0 20px;
  margin-bottom: 20px;
  border: 1px solid #e5e7ed;
  background: #f9fafc;
}
.consignee_address section{
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.consignee_address section p{
  font-size: 14px;
  line-height: 28px;
  color: #898989;
}
.consignee_address section p span{
  color: #616161;
}
.consignee_address section p span.consignee_name{
  font-weight: 600;
}
.consignee_address section p span.default_address{
  color: #ff8a6e;
  padding-left: 25px;
  cursor: pointer;
}
.consignee_btn{
  width: 68px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  margin-left: 15px;
  cursor: pointer;
}
.default_btn{
  color: #6389ed;
}
.edit_btn{
  color: #87ce76;
  border: 1px solid #87ce76;
  border-radius: 4px;
}
.delet_btn{
  color: #b3b3b3;
  border: 1px solid #e5e7ed;
  border-radius: 4px;
}



