
.main_wrapper_wrapper{
  background:#fff;
}
.main_header{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 1210px;
  height: 82px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
