  color: #fff;
  font-size: 14px;
}
.main_unlogin .icon-user{
  color: #d6d6d6;
  padding: 5px;
  margin-right: 5px;
  border: 1px solid #d6d6d6;
  border-radius: 100%;
}
.main_unlogin a{
  color: #616161;
}
.main_unlogin a:nth-child(2){
  padding-right: 10px;
  margin-right: 5px;
  border-right: 1px solid #d6d6d6;
}



