
.team_list{
  padding: 30px 20px;
}
.team_list ul li{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 98%;
  padding: 10px 0;
  margin: auto;
  border-bottom: 1px solid #eef0f6;
}
.team_list ul li:last-child{
  border: none;
}
.team_list ul li img{
  width: 140px;
  height: 200px;
}
.team_list ul li div{
  width: 750px;
  color: #898989;
  font-size: 15px;
  line-height: 30px;
}
.team_list ul li div span{
  font-weight: bold;
  color:#616161;
}



