
.indexNews {
  float: right;
  width: 590px;
  height: 304px;
  border: 1px solid #efefef;
}
.indexNews p {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  border-bottom: 1px solid #efefef;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #fcfcfc;
}
.indexNews p span {
  padding-left: 20px;
  font-size: 14px;
  font-weight: bold;
  color: #4a4a4a;
}
.indexNews a {
  padding-right: 20px;
  color: #cccccc;
  cursor: pointer;
}
.indexNews ul {
  width: 530px;
  margin: auto;
  margin-top: 10px;
  list-style: none;
  counter-reset: count;
}
.indexNews ul li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100%;
  height: 48px;
  border-bottom: 1px dashed #e5e7ed;
}
.indexNews ul li:last-child {
  border: 0;
}
.indexNews ul li:before {
  content: "● ";
  color: #b3b3b3;
}
.indexNews li span:nth-child(1) {
  font-size: 14px;
  color: #565656;
  padding: 0 10px;
}
.indexNews li span:nth-child(2) {
  font-size: 14px;
  color: #b3b3b3;
}



