
.help_select{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 550px;
  height: 32px;
  border: 2px solid #6398ed;
  margin: 30px auto 35px auto;
}
.help_select input{
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  border: 0;
  padding-left: 10px;
}
.help_select_btn{
  width: 80px;
  height: 32px;
  background: #6398ed;
  color: #fff;
  font-size: 15px;
  text-align: center;
  line-height: 32px;
}
.helpCentre ul{
  width: 925px;
  margin: auto;
}
.helpCentre ul li{
  width: 925px;
  height: 48px;
  line-height: 48px;
  border-bottom: 1px dashed #f1f1f1;
}
.helpCentre ul li a{
  display: block;
  padding-left: 30px;
  color: #b3b3b3;
  font-size: 15px;
}



