
.newBodys {
  float: left;
  width: 590px;
  height: 304px;
  border: 1px solid #efefef;
  border-top: 0;
}
.newBodys ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.newBodys ul li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  height: 40px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 14px;
  font-weight: bold;
  background: #fcfcfc;
  border: 1px solid #efefef;
  border-left: 0;
  color: #4a4a4a;
}
.newBodys ul li:nth-child(3) {
  border-right: 0;
}
.newBodys ul li.active {
  height: 38px;
  border-top: 3px solid #337ffb;
  border-bottom: 0;
  color: #337ffb;
  background: #ffffff;
}
.newBodyItem {
  float: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 270px;
  height: 100px;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border: 1px solid #efefef;
  margin-left: 20px;
  margin-top: 20px;
}
.newBodyItem:nth-child(2n + 1) {
  margin-left: 10px;
}
.newBodyItem img {
  width: 70px;
  height: 70px;
  margin: 15px;
  border-radius: 100%;
}
.newBodyItem p:nth-child(1) {
  font-size: 14px;
  color: #0b0b0b;
  line-height: 30px;
}
.newBodyItem p:nth-child(2) span {
  padding: 0 5px;
  line-height: 24px;
  font-size: 12px;
  color: #ccc;
  border: 1px solid #e1e1e1;
  margin-right: 5px;
}



