
.approve_table{
  width: 100%;
}
.approve_table td{
  height: 35px;
  vertical-align:middle;
  text-align: left;
  padding-left: 10px;
  border: 1px solid #eef0f6;
}
.approve_table td.long_td{
  width: 400px;
}
.passed{
  color: green;
}
.nopassed{
  color: #898989;
}
.passing{
  color: yellow;
}
.failpass{
  color: red;
}



